import React, { useState } from 'react';
import { useGame } from '../context/GameContext';
import { saveGame, loadGame, exportSave, importSave, deleteSave } from '../utils/saveSystem';

const MAZ = ({ onClose }) => {
    const { gameState, actions } = useGame();
    const [activeTab, setActiveTab] = useState('kwark');
    const [notification, setNotification] = useState('');
    const [exportData, setExportData] = useState('');
    const [importData, setImportData] = useState('');

    const showNotification = (message) => {
        setNotification(message);
        setTimeout(() => setNotification(''), 3000);
    };

    const handleSave = () => {
        const result = saveGame(gameState);
        showNotification(result.message);
    };

    const handleLoad = () => {
        const result = loadGame();
        if (result.success) {
            actions.loadGame(result.data);
            showNotification(result.message);
        } else {
            showNotification(result.message);
        }
    };

    const handleExport = () => {
        const result = exportSave(gameState);
        if (result.success) {
            setExportData(result.data);
            showNotification('Export pipraven! Zkoprujte kd ne.');
        } else {
            showNotification(result.message);
        }
    };

    const handleImport = () => {
        if (!importData.trim()) {
            showNotification('Zadejte importn kd.');
            return;
        }
        
        const result = importSave(importData.trim());
        if (result.success) {
            actions.loadGame(result.data);
            setImportData('');
            showNotification(result.message);
        } else {
            showNotification(result.message);
        }
    };

    const handleDelete = () => {
        if (window.confirm('Opravdu chcete smazat uloenou hru? Tato akce je nevratn.')) {
            const result = deleteSave();
            showNotification(result.message);
        }
    };

    const tabs = [
        { id: 'kwark', label: ' Prof. Kwark', icon: '' },
        { id: 'inventory', label: ' Invent', icon: '' },
        { id: 'fragments', label: ' Fragmenty', icon: '' },
        { id: 'save', label: ' Uloit/Nast', icon: '' }
    ];

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
            <div className={`max-w-2xl w-full max-h-[90vh] overflow-hidden rounded-xl shadow-2xl ${
                gameState.darkMode ? 'bg-gray-800 text-white' : 'bg-white text-gray-800'
            }`}>
                {/* Header */}
                <div className={`p-4 border-b ${
                    gameState.darkMode ? 'border-gray-700 bg-gray-900' : 'border-gray-200 bg-physics-blue'
                }`}>
                    <div className="flex justify-between items-center">
                        <h2 className={`text-xl font-bold flex items-center gap-2 ${
                            gameState.darkMode ? 'text-white' : 'text-white'
                        }`}>
                             Multifunkn Analytick Zznamnk (MAZ)
                        </h2>
                        <button
                            onClick={onClose}
                            className={`text-2xl hover:bg-opacity-20 hover:bg-gray-500 rounded p-1 transition-colors ${
                                gameState.darkMode ? 'text-gray-300' : 'text-white'
                            }`}
                        >
                            
                        </button>
                    </div>
                </div>

                {/* Tabs */}
                <div className={`flex border-b ${
                    gameState.darkMode ? 'border-gray-700' : 'border-gray-200'
                }`}>
                    {tabs.map(tab => (
                        <button
                            key={tab.id}
                            onClick={() => setActiveTab(tab.id)}
                            className={`flex-1 p-3 text-sm font-medium transition-colors ${
                                activeTab === tab.id
                                    ? gameState.darkMode 
                                        ? 'bg-gray-700 text-white border-b-2 border-physics-blue'
                                        : 'bg-blue-50 text-physics-blue border-b-2 border-physics-blue'
                                    : gameState.darkMode
                                        ? 'text-gray-400 hover:text-gray-200 hover:bg-gray-700'
                                        : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50'
                            }`}
                        >
                            <span className="block mb-1">{tab.icon}</span>
                            {tab.label}
                        </button>
                    ))}
                </div>

                {/* Content */}
                <div className="p-6 max-h-96 overflow-y-auto">
                    {/* Notification */}
                    {notification && (
                        <div className={`mb-4 p-3 rounded-lg ${
                            gameState.darkMode 
                                ? 'bg-green-900/50 text-green-300 border border-green-700'
                                : 'bg-green-100 text-green-800 border border-green-200'
                        }`}>
                            {notification}
                        </div>
                    )}

                    {/* Professor Kwark Tab */}
                    {activeTab === 'kwark' && (
                        <div className="space-y-4">
                            <div className={`p-4 rounded-lg ${
                                gameState.darkMode ? 'bg-gray-700' : 'bg-blue-50'
                            }`}>
                                <h3 className="font-semibold text-physics-blue mb-2">
                                     Profesor Kwark k:
                                </h3>
                                <div className={`italic ${
                                    gameState.darkMode ? 'text-gray-300' : 'text-gray-700'
                                }`}>
                                    {getKwarkMessage(gameState)}
                                </div>
                            </div>
                            
                            <div className="space-y-2">
                                <h4 className="font-medium">Dostupn npovdy:</h4>
                                <div className={`text-sm space-y-2 ${
                                    gameState.darkMode ? 'text-gray-300' : 'text-gray-600'
                                }`}>
                                    <p> Gravitace: "Sla, kter pitahuje objekty k sob. V ARFZ me bt obrcen!"</p>
                                    <p> Men: "Vdy si zm vzdlenosti a sly - sla nemus lht, ale mohou pekvapit."</p>
                                    <p> Experimenty: "Nejlep zpsob, jak pochopit fyziku, je vyzkouet si ji na vlastn ki."</p>
                                </div>
                            </div>
                        </div>
                    )}

                    {/* Inventory Tab */}
                    {activeTab === 'inventory' && (
                        <div className="space-y-4">
                            <h3 className="font-semibold text-lg">V invent:</h3>
                            <div className="grid grid-cols-1 gap-3">
                                {Object.entries(gameState.inventory)
                                    .filter(([_, hasItem]) => hasItem)
                                    .map(([item, _]) => (
                                        <div key={item} className={`p-3 rounded-lg border ${
                                            gameState.darkMode 
                                                ? 'border-gray-600 bg-gray-700' 
                                                : 'border-gray-200 bg-gray-50'
                                        }`}>
                                            <div className="flex items-center gap-3">
                                                <span className="text-2xl">{getItemIcon(item)}</span>
                                                <div>
                                                    <h4 className="font-medium">{getItemName(item)}</h4>
                                                    <p className={`text-sm ${
                                                        gameState.darkMode ? 'text-gray-400' : 'text-gray-600'
                                                    }`}>
                                                        {getItemDescription(item)}
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                                {Object.values(gameState.inventory).every(item => !item) && (
                                    <p className={`text-center italic ${
                                        gameState.darkMode ? 'text-gray-400' : 'text-gray-500'
                                    }`}>
                                        V invent je przdn.
                                    </p>
                                )}
                            </div>
                        </div>
                    )}

                    {/* Physics Fragments Tab */}
                    {activeTab === 'fragments' && (
                        <div className="space-y-4">
                            <div className="flex justify-between items-center">
                                <h3 className="font-semibold text-lg">Fyzikln fragmenty</h3>
                                <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                                    gameState.darkMode 
                                        ? 'bg-physics-purple/20 text-physics-purple' 
                                        : 'bg-physics-purple text-white'
                                }`}>
                                    {gameState.physicsFragments} zskno
                                </span>
                            </div>
                            
                            <div className={`p-4 rounded-lg ${
                                gameState.darkMode ? 'bg-gray-700' : 'bg-purple-50'
                            }`}>
                                <p className={`text-sm ${
                                    gameState.darkMode ? 'text-gray-300' : 'text-gray-700'
                                }`}>
                                    Fyzikln fragmenty jsou cenn kousky informac o ARFZ a skuten fyzice. 
                                    Sbrejte je prozkoumvnm svta a eenm hdanek!
                                </p>
                            </div>

                            {gameState.physicsFragments > 0 ? (
                                <div className="space-y-2">
                                    {Array.from({ length: gameState.physicsFragments }, (_, i) => (
                                        <div key={i} className={`p-3 rounded border ${
                                            gameState.darkMode 
                                                ? 'border-gray-600 bg-gray-700' 
                                                : 'border-gray-200 bg-white'
                                        }`}>
                                            <h4 className="font-medium"> Fragment #{i + 1}</h4>
                                            <p className={`text-sm ${
                                                gameState.darkMode ? 'text-gray-400' : 'text-gray-600'
                                            }`}>
                                                {getFragmentDescription(i)}
                                            </p>
                                        </div>
                                    ))}
                                </div>
                            ) : (
                                <p className={`text-center italic ${
                                    gameState.darkMode ? 'text-gray-400' : 'text-gray-500'
                                }`}>
                                    Zatm jste nenali dn fragmenty.
                                </p>
                            )}
                        </div>
                    )}

                    {/* Save/Load Tab */}
                    {activeTab === 'save' && (
                        <div className="space-y-6">
                            <div>
                                <h3 className="font-semibold text-lg mb-4">Sprva uloench her</h3>
                                <div className="grid grid-cols-2 gap-3">
                                    <button onClick={handleSave} className="physics-button">
                                         Uloit hru
                                    </button>
                                    <button onClick={handleLoad} className="physics-button">
                                         Nast hru
                                    </button>
                                </div>
                            </div>

                            <div>
                                <h4 className="font-medium mb-3">Export/Import</h4>
                                <div className="space-y-3">
                                    <button onClick={handleExport} className="physics-button-secondary w-full">
                                         Exportovat hru
                                    </button>
                                    {exportData && (
                                        <div>
                                            <label className="block text-sm font-medium mb-2">
                                                Exportn kd (zkoprujte):
                                            </label>
                                            <textarea
                                                value={exportData}
                                                readOnly
                                                className="physics-input w-full h-20 text-xs font-mono"
                                                onClick={(e) => e.target.select()}
                                            />
                                        </div>
                                    )}
                                    
                                    <div>
                                        <label className="block text-sm font-medium mb-2">
                                            Importn kd:
                                        </label>
                                        <textarea
                                            value={importData}
                                            onChange={(e) => setImportData(e.target.value)}
                                            placeholder="Vlote exportn kd zde..."
                                            className="physics-input w-full h-20 text-xs font-mono"
                                        />
                                        <button 
                                            onClick={handleImport} 
                                            className="physics-button-secondary w-full mt-2"
                                        >
                                             Importovat hru
                                        </button>
                                    </div>
                                </div>
                            </div>

                            <div className={`pt-4 border-t ${
                                gameState.darkMode ? 'border-gray-600' : 'border-gray-200'
                            }`}>
                                <button 
                                    onClick={handleDelete}
                                    className="bg-red-500 hover:bg-red-600 text-white font-medium py-2 px-4 rounded-lg transition-colors w-full"
                                >
                                     Smazat uloenou hru
                                </button>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

const getKwarkMessage = (gameState) => {
    if (gameState.physicsUnderstandingLevel === 0) {
        return "Ah! Nov student ve svt ARFZ! Neboj se, spolen prozkoumme tyto podivn fyzikln jevy. Zaneme pomalu s gravitac.";
    } else if (gameState.physicsUnderstandingLevel < 3) {
        return "Vborn pokrauje! Vidm, e zan chpat, jak funguj zkony fyziky v tto podivn realit. Pamatuj si - pozorovn je klem k pochopen.";
    } else {
        return "Jsem na tebe hrd! Tv chpn fyziklnch zkon se vrazn zlepilo. Mon jsi pipraven na sloitj vzvy v dalch znch ARFZ.";
    }
};

const getItemIcon = (item) => {
    const icons = {
        MAZ: '',
        mericiPasmo: '',
        vaha: '',
        teplomer: '',
        kamenPratelstvi: '',
        pamatniKamen: ''
    };
    return icons[item] || '';
};

const getItemName = (item) => {
    const names = {
        MAZ: 'Multifunkn Analytick Zzramnk',
        mericiPasmo: 'Mic psmo',
        vaha: 'Vha/Silomr',
        teplomer: 'Teplomr',
        kamenPratelstvi: 'Kmen ptelstv',
        pamatniKamen: 'Pamtn kmen od Zzzika'
    };
    return names[item] || item;
};

const getItemDescription = (item) => {
    const descriptions = {
        MAZ: 'V vrn spolenk v ARFZ. Poskytuje npovdy a provd vpoty.',
        mericiPasmo: 'Umouje pesn men vzdlenost v podivnm prosted ARFZ.',
        vaha: 'M hmotnost a sly - uiten pro gravitan experimenty.',
        teplomer: 'Sleduje teplotu v termodynamicky nestabilnch znch.',
        kamenPratelstvi: 'Dar od Energetickho Sketa. Umouje komunikaci na dlku.',
        pamatniKamen: 'Svtlkujc kmen na pamtku dobrodrustv v ARFZ.'
    };
    return descriptions[item] || 'Uiten nstroj pro fyzikln vzkum.';
};

