        "Kvantov mechanika v makrosvt: 'Objekty se zde chovaj kvantov i ve velkm mtku.'",
        "Gravitan anomlie: 'Sla je nepmo mrn hmotnosti - leh objekty padaj rychleji nahoru!'",
        "Men v ARFZ: 'Standardn jednotky zde plat, ale men mus bt interpretovno obrcen.'",
        "Energetick vzorce: 'E = mgh funguje, ale h me bt zporn v gravitanch anomlich.'",
