        "Vdeck metoda: 'I v podivnm svt plat - pozoruj, testuj, dokumentuj, opakuj.'",
        "Hmotnost vs. vha: 'V ARFZ se hmotnost nemn, ale vha me bt zporn!'",
        "Thov zrychlen: 'Normln 9.8 m/s, v ARFZ me bt -9.8 m/s nebo dokonce promnn.'",
