export const saveGame = (gameState) => {
    try {
        const saveData = {
            ...gameState,
            savedAt: new Date().toISOString(),
            version: "1.0.0"
        };
        localStorage.setItem('physicsAdventureSave', JSON.stringify(saveData));
        return { success: true, message: "Hra byla spn uloena!" };
    } catch (error) {
        console.error('Chyba pi ukldn hry:', error);
        return { success: false, message: "Chyba pi ukldn hry." };
    }
};

export const loadGame = () => {
    try {
        const savedGame = localStorage.getItem('physicsAdventureSave');
        if (!savedGame) {
            return { success: false, message: "dn uloen hra nebyla nalezena." };
        }
        
        const gameData = JSON.parse(savedGame);
        
        // Validate save data
        if (!gameData.currentSceneId || !gameData.inventory) {
            return { success: false, message: "Uloen hra je pokozen." };
        }
        
        return { success: true, data: gameData, message: "Hra byla spn natena!" };
    } catch (error) {
        console.error('Chyba pi natn hry:', error);
        return { success: false, message: "Chyba pi natn hry." };
    }
};

export const exportSave = (gameState) => {
    try {
        const exportData = {
            ...gameState,
            exportedAt: new Date().toISOString(),
            version: "1.0.0"
        };
        const exportString = btoa(JSON.stringify(exportData));
        return { success: true, data: exportString };
    } catch (error) {
        console.error('Chyba pi exportu hry:', error);
        return { success: false, message: "Chyba pi exportu hry." };
    }
};

export const importSave = (importString) => {
    try {
        const gameData = JSON.parse(atob(importString));
        
        // Validate imported data
        if (!gameData.currentSceneId || !gameData.inventory) {
            return { success: false, message: "Importovan data jsou neplatn." };
        }
        
        return { success: true, data: gameData, message: "Hra byla spn importovna!" };
    } catch (error) {
        console.error('Chyba pi importu hry:', error);
        return { success: false, message: "Chyba pi importu hry. Zkontrolujte formt dat." };
    }
};

export const deleteSave = () => {
    try {
        localStorage.removeItem('physicsAdventureSave');
        return { success: true, message: "Uloen hra byla smazna." };
    } catch (error) {
        console.error('Chyba pi mazn uloen hry:', error);
        return { success: false, message: "Chyba pi mazn uloen hry." };
    }
};